import React from "react";

const UpvoteSection=({articleName,upvotes,setArticleInfo}) =>{
    const upVoteArticle= async()=>{
        const result=await fetch(`/api/articles/${articleName}/upvotes`,{
            method:"POST",
        });
      const body=await result.json();
      setArticleInfo(body);
    }

    return(
        <div className="upvotes-section">
            <button onClick={()=>upVoteArticle()} >Upvote</button>
          <p>This articles have been upvoted {upvotes} times.</p>
        </div>
    );
}

export default UpvoteSection;