import React,{useState} from "react";

const AddComment=({articleName,setArticleInfo})=>{
    const [userName ,setUserName]=useState('');
    const [commentText ,setCommentText]=useState('');
    
    const AddNewComment= async()=>{
        const result= await fetch(`/api/articles/${articleName}/add-comment`,{
            method:"post",
            body:JSON.stringify({
                username:userName,text:commentText
            }),
            headers:{
                'Content-Type' : 'application/json',
            }
        });
        const body= await result.json();
        setArticleInfo(body);
        setCommentText('');
        setUserName('');
    }
    
    return(
         <div className="add-comment-form">
             <label>Name</label>
             <input type="text" value={userName} onChange={(event)=>setUserName(event.target.value)} ></input>
             <label>Comment</label>
             <textarea cols="6" value={commentText} onChange={(event)=>setCommentText(event.target.value)} ></textarea>
             <button onClick={()=>AddNewComment()}>Add Comment</button>
         </div>
    );
};





export default AddComment;