import React,{useState,useEffect} from "react";
import articleContents from "./Article-Contents";
import ArticleList from "../component/ArticleList";
import PageNotFound from "./PageNotFound";
import CommentList from "../component/CommentList";
import UpvoteSection from "../component/UpvoteSection";
import AddComment from "../component/AddCommentForm";


const ArticlePage =({ match } )=>{
    const name = match.params.name;
    const article=articleContents.find(article=>article.name === name)

    const otherArticles=articleContents.filter(article=>article.name!==name)
    const [articleInfo,setArticleInfo]=useState({ upvotes:0, comments:[] });
    
    useEffect(() => {
         const fetchData= async()=>{
              const result=await fetch(`/api/articles/${name}`);
              const body=await result.json();
              console.log(body);
            setArticleInfo(body);
         }
         fetchData();
    }, [name]);
     

    if(!article)
       return <h1><PageNotFound/></h1>
    console.log("name:"+name);
    return(
        <>
        <h1>{article.title}</h1>
           <UpvoteSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
           {
               article.content.map((paragraph,index)=>
                (
                    <p key={index}>{paragraph}</p>
                ))
           }
          <CommentList comments={articleInfo.comments} />
          <AddComment articleName={name} setArticleInfo={setArticleInfo} /> 
          <h2>Related Articles</h2>
          <p><ArticleList articles={otherArticles}/></p>
         

        </>
    );
}


export default ArticlePage;