import React from 'react';

const CommentList=({ comments }) =>(
     <>
      <h3>Comment List:</h3>
        {comments.map((para, key) => (
            
            <div className="comment" key={key}>
                <h4>{para.username}</h4>
                <p>{para.text}</p>

            </div>
        ))}
      </>
);

export default CommentList;